// D1RPGClient.js
export class D1RPGClient {
  constructor() {
    this.socket = null;
    this.room = null; // Current room name
    this.listeners = {
      message: [],
      roll: [],
      login: [],
      error: [],
      roomJoin: [],
      roomLeave: [],
      scene: [],
      narrate: [],
      clearMessages: [], // For clearing messages before loading history
      deleteMessage: [] // For handling message deletion
    };
    this.userId = null;
    this.reconnectAttempts = 0;
    this.maxReconnectAttempts = 5;
    this.reconnectDelay = 2000; // Start with 2 seconds
    this.isConnecting = false;
    this.messageQueue = [];
  }

  // Connect to WebSocket server
  async connect() {
    if (this.socket && (this.socket.readyState === WebSocket.OPEN || this.socket.readyState === WebSocket.CONNECTING)) {
      return; // Already connected or connecting
    }

    if (this.isConnecting) {
      return; // Already attempting to connect
    }

    this.isConnecting = true;

    try {
      // Connect to WebSocket server - replace with your worker's URL
      this.socket = new WebSocket('wss://rpg-chat-worker.raiyanrahmanxx.workers.dev/api/ws');
      
      // Set up event listeners
      this.socket.onopen = this._handleSocketOpen.bind(this);
      this.socket.onmessage = this._handleSocketMessage.bind(this);
      this.socket.onclose = this._handleSocketClose.bind(this);
      this.socket.onerror = this._handleSocketError.bind(this);
    } catch (error) {
      console.error("WebSocket connection error:", error);
      this._triggerEvent('error', { 
        context: 'connect', 
        message: error.message || 'failed to connect' 
      });
      this.isConnecting = false;
      this._attemptReconnect();
    }
  }

  _handleSocketOpen() {
    console.log("WebSocket connected");
    this.isConnecting = false;
    this.reconnectAttempts = 0;
    this.reconnectDelay = 2000;
    
    // If we have a user ID, send a re-login message
    if (this.userId) {
      console.log(`Sending re-login for ${this.userId}`);
      this._sendToServer({
        type: 'login',
        username: this.userId
      });
      
      // If we were in a room, rejoin it
      if (this.room) {
        console.log(`Rejoining room ${this.room}`);
        this._sendToServer({
          type: 'join',
          roomName: this.room
        });
      }
    }
    
    // Process any queued messages
    while (this.messageQueue.length > 0) {
      const message = this.messageQueue.shift();
      this._sendToServer(message);
    }
  }

  _handleSocketMessage(event) {
    try {
      const data = JSON.parse(event.data);
      console.log("Received WebSocket message:", data.type);
      
      switch (data.type) {
        case "message":
          this._triggerEvent('message', {
            sender: data.sender,
            text: data.text,
            type: data.messageType || 'chat',
            timestamp: data.timestamp
          });
          break;
          
        case "roll":
          this._triggerEvent('roll', {
            sender: data.sender,
            text: data.text,
            dice: data.dice,
            rolls: data.rolls,
            hits: data.hits,
            highEvenOdd: data.highEvenOdd,
            lowEvenOdd: data.lowEvenOdd,
            timestamp: data.timestamp
          });
          break;
          
        case "scene":
          this._triggerEvent('scene', {
            sender: data.sender,
            text: data.text,
            sceneName: data.sceneName,
            sceneType: data.sceneType,
            timestamp: data.timestamp
          });
          break;
          
        case "login":
          if (data.success) {
            this.userId = data.username;
            localStorage.setItem('rpgUsername', data.username);
            this._triggerEvent('login', {
              success: true,
              userId: data.username
            });
          } else {
            this._triggerEvent('error', {
              context: 'login',
              message: data.message || 'login failed'
            });
          }
          break;
          
        case "roomJoin":
          if (data.success) {
            this.room = data.roomName;
            localStorage.setItem('rpgRoom', data.roomName);
            
            // Clear messages before joining new room
            this._triggerEvent('clearMessages', {});
            
            this._triggerEvent('roomJoin', {
              roomId: data.roomName,
              name: data.roomName
            });
          } else {
            this._triggerEvent('error', {
              context: 'join',
              message: data.message || 'failed to join room'
            });
          }
          break;
          
        case "roomLeave":
          if (data.success) {
            this.room = null;
            localStorage.removeItem('rpgRoom');
            
            // Clear messages when leaving room
            this._triggerEvent('clearMessages', {});
            
            this._triggerEvent('roomLeave');
          } else {
            this._triggerEvent('error', {
              context: 'leave',
              message: data.message || 'failed to leave room'
            });
          }
          break;
          
        case "deleteMessage":
          if (data.success) {
            this._triggerEvent('deleteMessage', {
              success: true
            });
            
            this._triggerEvent('message', {
              sender: "system",
              text: "last message deleted",
              type: "system",
              system: true,
              timestamp: Date.now(),
              temporary: true
            });
          } else {
            this._triggerEvent('error', {
              context: 'delete',
              message: data.message || 'failed to delete message'
            });
          }
          break;
          
        case "error":
          this._triggerEvent('error', {
            context: data.context || 'server',
            message: data.message || 'unknown error'
          });
          break;
          
        case "history":
          console.log("History received with", data.messages?.length || 0, "messages");
          
          // Handle message history - clear existing messages first if we have messages
          if (Array.isArray(data.messages)) {
            data.messages.forEach(msg => {
              console.log("Processing history message:", msg.type);
              
              if (msg.type === "roll") {
                this._triggerEvent('roll', {
                  sender: msg.sender,
                  text: msg.text,
                  dice: msg.metadata?.dice,
                  rolls: msg.metadata?.rolls,
                  hits: msg.metadata?.hits,
                  highEvenOdd: msg.metadata?.highEvenOdd,
                  lowEvenOdd: msg.metadata?.lowEvenOdd,
                  timestamp: msg.timestamp,
                  historical: true
                });
              }
              else if (msg.type === "scene") {
                this._triggerEvent('scene', {
                  sender: msg.sender,
                  text: msg.text,
                  sceneName: msg.metadata?.sceneName,
                  sceneType: msg.metadata?.sceneType || "regular",
                  timestamp: msg.timestamp,
                  historical: true
                });
              }
              else if (msg.type === "narrate") {
                this._triggerEvent('message', {
                  sender: msg.sender,
                  text: msg.text,
                  type: 'narrate',
                  timestamp: msg.timestamp,
                  historical: true
                });
              }
              else {
                this._triggerEvent('message', {
                  sender: msg.sender,
                  text: msg.text,
                  type: msg.type,
                  timestamp: msg.timestamp,
                  historical: true
                });
              }
            });
          }
          break;
      }
    } catch (error) {
      console.error('Error processing WebSocket message:', error);
    }
  }

  _handleSocketClose(event) {
    console.log("WebSocket closed", event.code, event.reason);
    this.isConnecting = false;
    
    // Only attempt to reconnect if it wasn't a clean close
    if (!event.wasClean) {
      this._attemptReconnect();
    }
  }

  _handleSocketError(error) {
    console.error("WebSocket error", error);
    this.isConnecting = false;
    this._triggerEvent('error', { 
      context: 'socket', 
      message: 'websocket error' 
    });
    
    this._attemptReconnect();
  }

  _attemptReconnect() {
    if (this.reconnectAttempts >= this.maxReconnectAttempts) {
      this._triggerEvent('error', { 
        context: 'reconnect', 
        message: 'maximum reconnection attempts reached' 
      });
      return;
    }
    
    this.reconnectAttempts++;
    
    // Exponential backoff
    const delay = Math.min(30000, this.reconnectDelay * Math.pow(1.5, this.reconnectAttempts - 1));
    console.log(`Attempting to reconnect in ${delay}ms (attempt ${this.reconnectAttempts})`);
    
    setTimeout(() => {
      this.connect();
    }, delay);
  }

  _sendToServer(data) {
    if (!this.socket || this.socket.readyState !== WebSocket.OPEN) {
      // Queue the message for when connection is established
      console.log("Queueing message for later sending", data);
      this.messageQueue.push(data);
      
      // Try to reconnect if not already connecting
      if (!this.isConnecting && (!this.socket || this.socket.readyState !== WebSocket.CONNECTING)) {
        this.connect();
      }
      return;
    }
    
    console.log("Sending to server:", data);
    this.socket.send(JSON.stringify(data));
  }

  // Authentication
  async login(username) {
    if (!username) {
      this._triggerEvent('error', { 
        context: 'login', 
        message: 'username is required' 
      });
      return { success: false };
    }
    
    console.log(`Attempting to login as ${username}`);
    
    // Connect if not already connected
    await this.connect();
    
    this._sendToServer({
      type: 'login',
      username: username
    });
    
    // Store username for reconnects
    this.userId = username;
    localStorage.setItem('rpgUsername', username);
    
    return { success: true, userId: username };
  }

  async loginWithStored() {
    const username = localStorage.getItem('rpgUsername');
    
    if (!username) {
      return { success: false };
    }
    
    console.log(`Found stored username: ${username}`);
    
    // Just update the local state, don't trigger normal login
    this.userId = username;
    
    // Connect if not already connected
    await this.connect();
    
    // Send login message directly
    this._sendToServer({
      type: 'login',
      username: username
    });
    
    // If we have a stored room, join it after a delay
    const roomName = localStorage.getItem('rpgRoom');
    if (roomName) {
      console.log(`Found stored room: ${roomName}`);
      
      // Slight delay to ensure login completes first
      setTimeout(() => {
        this.joinRoom(roomName);
      }, 500);
    }
    
    return { success: true, userId: username };
  }

  logout() {
    console.log("Logging out");
    this.userId = null;
    this.room = null;
    localStorage.removeItem('rpgUsername');
    localStorage.removeItem('rpgRoom');
    
    // Close socket connection
    if (this.socket) {
      this.socket.close();
      this.socket = null;
    }
    
    return { success: true, logout: true };
  }
  
  // Room management
  async joinRoom(roomName) {
    if (!this.userId) {
      this._triggerEvent('error', { 
        context: 'joinRoom', 
        message: 'not logged in' 
      });
      return false;
    }

    console.log(`Joining room: ${roomName}`);
    
    this._sendToServer({
      type: 'join',
      roomName: roomName
    });
    
    return true;
  }
  
  async leaveRoom() {
    if (!this.userId) {
      this._triggerEvent('error', { 
        context: 'leaveRoom', 
        message: 'not logged in' 
      });
      return false;
    }
    
    if (!this.room) {
      this._triggerEvent('error', { 
        context: 'leaveRoom', 
        message: 'not in a room' 
      });
      return false;
    }

    console.log(`Leaving room: ${this.room}`);
    
    this._sendToServer({
      type: 'leave'
    });
    
    return true;
  }
  
  // Delete last message
  async deleteLastMessage() {
    if (!this.userId) {
      this._triggerEvent('error', { 
        context: 'deleteMessage', 
        message: 'not logged in' 
      });
      return false;
    }
    
    if (!this.room) {
      this._triggerEvent('error', { 
        context: 'deleteMessage', 
        message: 'not in a room' 
      });
      return false;
    }

    console.log(`Deleting last message in room: ${this.room}`);
    
    this._sendToServer({
      type: 'deleteMessage'
    });
    
    return true;
  }

  // Messaging
  async sendMessage(text, type = 'chat') {
    if (!this.userId) {
      this._triggerEvent('error', { 
        context: 'sendMessage', 
        message: 'not logged in' 
      });
      return false;
    }
    
    if (!this.room) {
      this._triggerEvent('error', { 
        context: 'sendMessage', 
        message: 'not in a room' 
      });
      return false;
    }

    this._sendToServer({
      type: 'message',
      messageType: type,
      text: text
    });
    
    return true;
  }
  
  // Scene creation
  async sendScene(sceneName, sceneType = 'regular') {
    if (!this.userId) {
      this._triggerEvent('error', { 
        context: 'sendScene', 
        message: 'not logged in' 
      });
      return false;
    }
    
    if (!this.room) {
      this._triggerEvent('error', { 
        context: 'sendScene', 
        message: 'not in a room' 
      });
      return false;
    }

    this._sendToServer({
      type: 'scene',
      sceneName: sceneName,
      sceneType: sceneType
    });
    
    return true;
  }
  
  // Narration
  async sendNarration(text) {
    if (!this.userId) {
      this._triggerEvent('error', { 
        context: 'sendNarration', 
        message: 'not logged in' 
      });
      return false;
    }
    
    if (!this.room) {
      this._triggerEvent('error', { 
        context: 'sendNarration', 
        message: 'not in a room' 
      });
      return false;
    }

    this._sendToServer({
      type: 'message',
      messageType: 'narrate',
      text: text
    });
    
    return true;
  }
  
  // Dice rolling
  async sendRoll(notation) {
    if (!this.userId) {
      this._triggerEvent('error', { 
        context: 'sendRoll', 
        message: 'not logged in' 
      });
      return false;
    }
    
    if (!this.room) {
      this._triggerEvent('error', { 
        context: 'sendRoll', 
        message: 'not in a room' 
      });
      return false;
    }

    const rollResult = this.rollDice(notation);
    if (!rollResult) {
      this._triggerEvent('error', { 
        context: 'sendRoll', 
        message: 'invalid dice notation' 
      });
      return false;
    }
    
    // Count hits (even numbers)
    const hits = rollResult.rolls.filter(roll => roll % 2 === 0).length;
    
    // Find highest and lowest rolls
    const highest = Math.max(...rollResult.rolls);
    const lowest = Math.min(...rollResult.rolls);
    
    // Determine if highest and lowest are even or odd
    const highEvenOdd = highest % 2 === 0 ? 'even' : 'odd';
    const lowEvenOdd = lowest % 2 === 0 ? 'even' : 'odd';
    
    // Format roll text with hit count and high/low even/odd status
    const rollText = `🎲 @${this.userId} ${notation}: [${rollResult.rolls.join('][')}] = ${hits} hit${hits !== 1 ? 's' : ''}, high ${highEvenOdd}, low ${lowEvenOdd}`;
    
    this._sendToServer({
      type: 'roll',
      dice: notation,
      rolls: rollResult.rolls,
      hits: hits,
      highEvenOdd: highEvenOdd,
      lowEvenOdd: lowEvenOdd,
      text: rollText
    });
    
    return true;
  }

  rollDice(notation) {
    // Parse dice notation like "2d6"
    const match = notation.match(/^(\d+)d(\d+)$/);
    if (!match) return null;
    
    const count = parseInt(match[1], 10);
    const sides = parseInt(match[2], 10);
    
    // Validate limits to prevent abuse
    if (count > 100 || sides > 1000) return null;
    
    const rolls = Array(count).fill(0).map(() => 
      Math.floor(Math.random() * sides) + 1
    );
    
    return {
      rolls,
      total: rolls.reduce((a, b) => a + b, 0)
    };
  }

  // Command processing
  async processCommand(input, type = 'chat') {
    if (!input) return false;
    
    console.log(`Processing command: ${input}`);
    
    // Process commands
    if (input.startsWith('/login ')) {
      const username = input.split(' ')[1];
      if (!username) {
        this._triggerEvent('error', { 
          context: 'command', 
          message: 'usage: /login username' 
        });
        return { success: false };
      }
      
      return await this.login(username);
    }
    
    else if (input.startsWith('/join ')) {
      const roomName = input.split(' ')[1];
      if (!roomName) {
        this._triggerEvent('error', { 
          context: 'command', 
          message: 'usage: /join roomname' 
        });
        return false;
      }
      
      return await this.joinRoom(roomName);
    }
    
    else if (input.startsWith('/leave')) {
      return await this.leaveRoom();
    }
    
    else if (input.startsWith('/delete')) {
      return await this.deleteLastMessage();
    }
    
    else if (input.startsWith('/logout')) {
      return this.logout();
    }
    
    else if (input.startsWith('/roll ')) {
      const notation = input.split(' ')[1];
      if (!notation || !notation.match(/^\d+d\d+$/)) {
        this._triggerEvent('error', { 
          context: 'command', 
          message: 'usage: /roll XdY (e.g., /roll 2d6)' 
        });
        return false;
      }
      
      return await this.sendRoll(notation);
    }
    
    else if (input.startsWith('/scene ')) {
      // Get scene name (everything after /scene)
      const sceneName = input.substring(7).trim();
      if (!sceneName) {
        this._triggerEvent('error', { 
          context: 'command', 
          message: 'usage: /scene Scene Name' 
        });
        return false;
      }
      
      return await this.sendScene(sceneName, 'regular');
    }
    
    else if (input.startsWith('/narrate ')) {
      // Get narration text (everything after /narrate)
      const narrationText = input.substring(9).trim();
      if (!narrationText) {
        this._triggerEvent('error', { 
          context: 'command', 
          message: 'usage: /narrate Your narration text' 
        });
        return false;
      }
      
      return await this.sendNarration(narrationText);
    }
    
    else if (input.startsWith('/')) {
      this._triggerEvent('error', { 
        context: 'command', 
        message: 'unknown command' 
      });
      return false;
    }
    
    // Regular message - check if it starts with "narrate" to automatically convert to narration
    else {
      if (input.toLowerCase().startsWith('narrate ')) {
        return await this.sendNarration(input.substring(8).trim());
      } else {
        return await this.sendMessage(input, type);
      }
    }
  }

  // Event listeners
  on(event, callback) {
    if (this.listeners[event]) {
      this.listeners[event].push(callback);
    }
    return this; // For chaining
  }

  off(event, callback) {
    if (this.listeners[event]) {
      this.listeners[event] = this.listeners[event].filter(cb => cb !== callback);
    }
    return this; // For chaining
  }

  _triggerEvent(event, data) {
    if (this.listeners[event]) {
      this.listeners[event].forEach(callback => callback(data));
    }
  }
}